@keyframes light-bulb {
	to {
		fill-opacity: 1;
		fill: #FBF8CA;
	}
}

@keyframes light-lamp {
	0% {
		transform: rotate(5deg)
	}

	50% {
		transform: rotate(-5deg)
	}

	100% {
		transform: rotate(5deg)
	}
}

@keyframes relay-line {
	0% {
		transform: translateY(0);
	}

	10% {
		transform: translateY(10px);
	}

	40% {
		transform: translateY(-25px);
	}

	60% {
		transform: translateY(-25px);
	}

	85% {
		transform: translateY(10px);
	}

	100% {
		transform: translateY(0px);
	}
}

@keyframes relay-click {
	0% {
		transform: scale(1);
	}

	50% {
		transform: scale(.9);
	}

	100% {
		transform: scale(1);
	}
}