// ! font-size
@for $size from 8 to 72 {
	@if $size % 2==0 {
		.font-size-#{$size} {
			font-size: #{$size}px !important;
		}
	}
}

// ! font-weight
$fontWeights: 100, 200, 300, 400, 500, 600, 650, 700, 750, 800, 850, 900, 950, normal, bold, lighter, bolder;

@each $weight in $fontWeights {
	.font-weight-#{$weight} {
		font-weight: $weight !important;
	}
}

// ! font-style
$fontStyles: normal,
italic,
oblique;

@each $style in $fontStyles {
	.font-style-#{$style} {
		font-style: $style !important;
	}
}

// ! Cursor
$cursors: not-allowed, auto, default, none, pointer, progress, text, col-resize, row-resize, ew-resize, ns-resize;

@each $cursor in $cursors {
	.cursor-#{$cursor} {
		cursor: $cursor !important;
	}
}

// ! opacity
@for $opacity from 0 to 101 {
	@if $opacity % 10==0 {
		.opacity-#{$opacity} {
			opacity: #{math.div($opacity, 100)} !important;
		}
	}
}

div[class*=SnackbarItem-contentRoot] {
	font-size: 12px;
	font-weight: 500;
}