.inputGroup {
	width: 100%;

	label {
		width: 100%;
	}
}

.label {
	display: flex;
	align-items: center;
	justify-content: space-between;
	color: rgb(250, 250, 250);
	margin-bottom: 8px;
	font-size: 12px;

	span {
		color: inherit;
	}

	a {
		font-size: 12px;
		color: rgb(242, 185, 12);
		font-weight: 500;
	}
}

.inputBox {
	width: 100%;
	border: 1px solid rgb(66, 66, 66);
	border-radius: 4px;
	height: 46px;
	display: flex;
	justify-content: space-between;
	overflow: hidden;
	background-color: transparent;

	input {
		flex: 1;
		letter-spacing: 0.5px;
		padding: 0 16px;
		border: 0;
		font-size: 12px;
		height: 100%;
		background-color: transparent;
		color: rgb(250, 250, 250);

		&::placeholder {
			color: rgb(224, 224, 224);
		}

		&:disabled {
			cursor: not-allowed;
		}
	}

	&.focus {
		border-color: rgb(224, 224, 224);
	}

	&.error {
		border-color: rgb(211, 47, 47);
	}
}

.iconBtn {
	color: rgb(224, 224, 224);
	border: 0;
	background-color: transparent;
	display: flex;
	width: 46px;
	align-items: center;
	justify-content: center;

	svg {
		width: 16px;
		height: 16px;
	}
}

.errorMsg {
	color: rgb(211, 47, 47);
	font-size: 12px;
	font-weight: 400;
	margin-top: 4px;
	line-height: 2;
}